@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/css?family=Noto+Sans&display=swap);
body {
    font-family: 'Open Sans', Arial, Helvetica, sans-serif !important;
}
.navbar {
    margin-bottom: 3rem;
}

.navbar svg {
    margin-right: 0.5rem;
}

.navbar-brand {
    font-size: 2rem !important;
    font-weight: 700;
}

.navbar-brand img {
    max-height: 80px;
    margin-right: 15px;
}

.nav-link {
    font-size: 1.2rem;
}

.donation-form {
    background-color: #343a40;
    padding: 2rem 2rem;
    color: #fff;
    border-radius: 9px;
}

.donation-form h2 {
    margin-bottom: 1rem;
    font-weight: 700;
}

.donation-form hr {
    border-top: 1px solid rgba(255, 255, 255, 0.6) !important;
}

.donation-form .btn {
    font-size: 1.2rem;
    padding: 0.5rem 5rem;
    margin-top: 1rem;
}
* {
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Noto Sans', Arial, Helvetica, sans-serif;
  background: #222222;
  color: #fff;
}

.alert-color {
    color: #007bff;
}

#alert {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    text-align: center;
    text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.9);
    color: #fff;
}

.donation {
    display: block;
    font-size: 32px;
    font-weight: 600;
    width: 100%;
}

.message {
    display: block;
    font-size: 24px;
    font-weight: 600;
    width: 100%;
    margin-top: 5px;
}
